<template>

    <div :style="style">
        <div style="margin-bottom: 25px">
            <i class="el-icon-plus"></i><span>添加</span>
            <div style="margin-top:10px;">
                <el-button type="primary" @click="addStudent()" plain>添加</el-button>
            </div>
        </div>
        <div>
            <div>
                <i class="el-icon-search"></i><span>查询</span>
                <div class='searchContent' style="margin-top:10px;">
                    <el-form :model="searchData" inline>
                        <el-form-item label="学号:">
                            <el-input v-model.trim="searchData.studentId"></el-input>
                        </el-form-item>
                        <el-form-item label="姓名:">
                            <el-input v-model.trim="searchData.studentName"></el-input>
                        </el-form-item>
                        <el-form-item label="课程名:">
                            <el-input v-model.trim="searchData.courseName"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search" plain>查询</el-button>
                            <el-button type="success" @click="examineAll" plain>重置</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
            <div>
                <div>
                    <i class="el-icon-document"><span>学生列表</span></i>
                </div>
            </div>

        </div>
        <el-table
                :data="scoreList"
                style="width: 100%"
                height="100%">
            <el-table-column
                    fixed
                    prop="studentId"
                    label="学号"
            >
            </el-table-column>
            <el-table-column
                    fixed
                    prop="studentName"
                    label="姓名"
            >
            </el-table-column>

            <el-table-column
                    fixed
                    prop="courseName"
                    label="课程名"
            >
            </el-table-column>

            <el-table-column
                    fixed
                    prop="score"
                    label="分数"
            >
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
            >
                <template slot-scope="scope">
                    <el-button @click="updateStudent(scope.row)" type="text" size="small">修改</el-button>
                    <el-button type="text" size="small" @click="deleteStudent(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                background
                layout="prev, pager, next"
                @current-change="handlePageChange"
                :total="pageInfo.total"
                :page-size="pageInfo.pageSize"
                :current-page="pageInfo.pageNum">
        </el-pagination>
        <transition name="fade" mode="out-in">
            <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible"
                       v-loading="loading"
                       element-loading-text="拼命加载中"
                       element-loading-spinner="el-icon-loading"
                       element-loading-background="rgba(0, 0, 0, 0.8)">
                <div v-if="dialogTitle === '修改信息'">
                    <el-form :model="score" >
                        <el-form-item label="学号" :label-width="formLabelWidth">
                            <el-input v-model="score.studentId" autocomplete="on" size="normal" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="姓名" :label-width="formLabelWidth">
                            <el-input v-model="score.studentName" autocomplete="on" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="课程名" :label-width="formLabelWidth">
                            <el-input v-model="score.courseName" autocomplete="on" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="分数" :label-width="formLabelWidth">
                            <el-input v-model="score.score" autocomplete="on"></el-input>
                        </el-form-item>
                    </el-form>
                </div>
                <div v-else>
                    <el-form :model="score" >
                        <el-form-item label="学号" :label-width="formLabelWidth">
                            <el-input v-model="score.studentId" autocomplete="on" size="normal"></el-input>
                        </el-form-item>
                        <el-form-item label="姓名" :label-width="formLabelWidth">
                            <el-input v-model="score.studentName" autocomplete="on"></el-input>
                        </el-form-item>
                        <el-form-item label="课程名" :label-width="formLabelWidth">
                            <el-input v-model="score.courseName" autocomplete="on"></el-input>
                        </el-form-item>
                        <el-form-item label="分数" :label-width="formLabelWidth">
                            <el-input v-model="score.score" autocomplete="on"></el-input>
                        </el-form-item>
                    </el-form>
                </div>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="commitMessage()">确 定</el-button>
                </div>
            </el-dialog>
        </transition>
    </div>

</template>

<script>
// import axios from 'axios'
export default {
  name: 'page',
  data () {
    return {
      loading: false,
      style: {
        height: window.innerHeight + 'px'
      },
      dialogTitle: '',
      dialogFormVisible: false,
      formLabelWidth: '120px',
      scoreList: [
        {
          studentId: '1', // 学生ID
          studentName: 'dsaf', // 学生姓名
          courseName: 'fdsa ', // 性别
          score: '122' // 年级
        },
        {
          studentId: '34', // 学生ID
          studentName: 'fdax', // 学生姓名
          courseName: 'fgff', // 性别
          score: '31' // 年级
        }
      ],

      score: {
        studentId: '', // 学生ID
        studentName: '', // 学生姓名
        courseName: '', // 性别
        score: '' // 年级

      },
      searchData: { // 搜索的数据
        studentId: '', // 学生ID
        studentName: '', // 学生姓名
        courseName: '' // 性别
      },
      pageInfo: {
        total: 100,
        pageSize: 15,
        pageNum: 1
      }
    }
  },
  methods: {
    /**
             * 修改页数
             */
    handlePageChange (val) {
      this.pageInfo.pageNum = val
      this.getScoreList()
    },
    /**
             * 添加学生
             */
    addStudent () {
      this.dialogFormVisible = true
      this.score = {}
      this.dialogTitle = '添加学生'
    },
    /**
             * 提交参数
             */
    commitMessage () {
      if (this.dialogTitle === '修改信息') {
        this.loading = true
        let data = this.score
        let callback = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('成功', 'success', '修改成功')
        }
        let error = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('警告', 'warning', '修改失败')
        }
        let exception = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('错误', 'error', '请求错误')
        }
        this._ajax('post', '/score/updateScore', callback, {}, error, exception, data)
      } else {
        this.loading = true
        let data = this.score

        let callback = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('成功', 'success', '添加成功')
        }
        let error = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('警告', 'warning', '添加失败')
        }
        let exception = (res) => {
          console.log(res)
          this.loading = false
          this.dialogFormVisible = false
          this._notify('错误', 'error', '请求错误')
        }
        this._ajax('post', '/score/addScore', callback, {}, error, exception, data)
      }
    },
    /**
             * 更新学生信息
             * @param student 学生
             */
    updateStudent (student) {
      this.dialogFormVisible = true
      this.score = student
      this.dialogTitle = '修改信息'
    },
    /**
             * 删除学生
             * @param student 学生
             */
    deleteStudent (student) {
      let callback = () => {
        let data = student
        let callback = (res) => {
          console.log(res)
          this._alert('删除成功！', 'success', 2000)
          this.getScoreList()
        }

        let error = (res) => {
          console.log(res)
        }

        this._ajax('post', '/score/deleteScore', callback, {}, error, () => {}, data)
      }
      let error = () => {
        this._alert('已取消', 'info', 2000)
      }
      this._confirm('您确定删除它吗？', callback, error)
    },
    /**
             * 搜索学生
             */
    search () {
      if (this.searchData.studentId === '' && this.searchData.studentName === '' &&
                    this.searchData.courseName === '') {
        this.getScoreList()
      } else {
        let params = this.searchData
        let callback = (res) => {
          console.log(res)
          this.scoreList = res.charData
        }
        let error = (res) => {
          console.log(res)
          this.scoreList = []
        }
        this._ajax('post', '/score/getByStudent', callback, {}, error, () => {}, params)
      }
    },
    /**
             * 重置查询内容
             */
    examineAll () {
      this.searchData = {}
    },
    getScoreList () {
      let callback = (res) => {
        this.scoreList = res.charData.list
        console.log(this.scoreList.length)
        this.pageInfo.pageSize = res.charData.pageSize
        this.pageInfo.total = res.charData.total
      }
      let error = (res) => {
        console.log(res)
      }
      let param = this.pageInfo
      this._ajax('get', '/score/getAll', callback, param, error, () => {})
    }
  },
  mounted () {
    console.log(this.platform)
    this.getScoreList()
  }
}
</script>

<style scoped>
    .btnFun {
        text-align: center;
        margin: 50px;
    }
    .bounce-enter-active {
        animation: bounce-in .5s;
    }
    .bounce-leave-active {
        animation: bounce-in .5s reverse;
    }
    @keyframes bounce-in {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1.5);
        }
        100% {
            transform: scale(1);
        }
    }
</style>



